.contact-container {
    padding: 2%;
    /* background-color: #7EA3F1; */
    display: flex;
    flex-wrap: wrap; 
    justify-content: center; 
    gap: 35px;
}


.contact {
    text-align: center;
    margin-top: 2%;
    padding-bottom: 20px; 
}

.contact h2 {
    padding-top: 3%;
    font-size: 25px;
    text-shadow: 0px 0px 15px red;

}

.contact h4 {
    font-size: medium;
    font-style: italic;
    padding-left: 3%;
    padding-right: 3%;
    
}


.contact img:hover {
    transform: scale(1.1) translateY(3px);
    transition: all .2s ease-in-out;
    
}