.section{
    padding: 70px 10% 0 25%;
    /* background-repeat: no repeat; */
    background-size: cover;
}

.home{
    width: 100%;
    min-height: 80vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
    align-items: center;   
    /* margin: 0 2%; */
}

.home-text img{
    margin-bottom: 10px;
}


.home-img img{
    max-width: 100%;
    width: 60%;
    height: 70vh;
}

.btn3 {
    background-color: #7678ed;
    border-radius: 50px;
    padding: 5px 8px;
    font-style: italic; 
    border: 2px solid   ;
    color: white;
    text-decoration: none;    
}

.btn3:hover{
    box-shadow: 0 0 15px #ff006e;   
}

