@media (max-width: 1560px) {
    :root {
        --big-font: 3.5rem;
        --h2-font: 2rem;
        
    }  
}

@media (max-width: 1000px) {
    section{
        padding: 90px 8%;
    }    
    .home{
        grid-template-columns: 1fr;
        text-align: center;
    }
}

@media (max-width: 720px) { 
    section{
        padding: 100px 7%;
    }  
}

@media (max-width: 600px) { 
    section{
        padding-right: 30% ;
        /* margin: 0px; */
    }  
    .home-img img{
        width: 100%;
        height: 80vh;
        margin-left: 40px;
    }
    .home-text img{
        width: 110%;
        height: 80vh;
    }   

}

@media (max-width: 500px) {
    .home-text img{
        width: 95%;
    } 
}

@media (max-width: 450px) {
    .home-text img {
        height: 80px;
        width: 110%;
    }
}

@media (max-width: 400px) {
    .home-text img {
        width: 120%;
        height: 50vh;
    }
}
