*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important;
    scroll-behavior: smooth;
    list-style: none;
    text-decoration: none;
    border: none;
    color:#fff;
}

#navbarNav a:hover{
    color:#7678ed;
}


.navbar {
    display: flex;
    justify-content: end;
    align-items: end;
    /* padding: 10px 50px; */
    background-color: #7EA3F1;
    color: #fff;
}

.navbar-brand {
    font-size: 26px;
    font-weight: bold;
    color: #fff;
    margin-left: 50px;
}

/* .navbar-brand:hover,
.navbar-brand:focus {
    color:#7678ed;
} */

.navbar-nav {
    gap: 10px;
}

.nav-item {
    text-decoration: none;
    color: #fff;
}

.nav-link {
    color: #fff;
    position: relative;
    text-decoration: none;
}

.cart-icon {
    font-size: 25px;
    cursor: pointer;
    margin-right: 50px;
    margin-left: 15px;
}

.cart-count {
    position: absolute;
    right: -5px;
    background-color: #dd2d4a;
    color: white;
    border-radius: 47%;
    padding: 0 5px;
    font-size: 12px;
}

.logout-button {
    padding: 5px 5px;
}

