.menu{
  background-color: #C8D7F9;
}

.menu-title {
    color: #7678ed;
    /* margin-top: 1%; */
    font-style: italic;
    padding-top: 1%;
    text-align: center;
    font-weight: bold;
}


.food-menu {
    margin-top: 2%;
    padding: 20px;
    display: flex;
    flex-wrap: wrap; 
    justify-content: center; 
    gap: 25px;
    flex-basis: 1rem; 
    align-self: normal;
    background-color: #7EA3F1;
  }

  .card{
    width: 18rem;
    text-align: center;
    font-style: italic;
    border-radius: 20px;
    /* box-shadow: 0 0 15px #688ce1;   */
    border: 1px solid #7EA3F1; 
 
  }
  
  .card:hover {
    /* border: 2px solid red;  */
    transition: .2s ease-in-out;
    transform: scale(1.1);
  }

  .card-body {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100%;
    flex-wrap: wrap; 
  }

  .btn1 {
    margin-top: auto;
    border-radius: 50px;
    font-size: 14px;
    background-color: #7678ed;
    border: 3px solid #7678ed;
    padding: 3px 20px;
  }

  .btn1:active{
    box-shadow: 0 0 15px #ff006e;   
}
  
